@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	font-family: "Roboto", sans-serif;
}
li {
	list-style: none;
}
.loading {
	position: absolute;
	top: 0;
	width: 100vw;
	height: 100vh;
	display: flex;
	align-items: center;
	justify-content: center;
	background: #666;
	opacity: 0.5;
	font-size: 3rem;
	color: white;
}
.error {
	width: 25%;
	height: 3rem;
	margin: 0 auto;
	background: #ff6c68;
	box-shadow: 1px 1px 1px #ff6c68;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 1.5rem;
	font-weight: bold;
	color: black;
	border-radius: 10px;
	flex-wrap: wrap;
}
.success {
	width: 25%;
	height: 3rem;
	margin: 0 auto;
	background: #51d62d;
	box-shadow: 1px 1px 1px #51d62d;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 1.5rem;
	font-weight: bold;
	color: black;
	border-radius: 10px;
	flex-wrap: wrap;
}
.navbar {
	font-size: 1.5rem;
	width: 100%;
	height: 5rem;
	background: #333;
	display: flex;
	align-items: center;
	justify-content: space-around;
	color: white;
	margin-bottom: 2rem;
}
.logo {
	width: 29%;
	margin: 0 2rem;
}
.navbar-ul {
	width: 70%;
	display: flex;
	align-items: center;
	justify-content: flex-end;
}
.navbar-ul li {
	cursor: pointer;
	margin: 0 2rem;
}
.item {
	width: 50%;
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin: 1.5rem auto;
}
.form {
	display: flex;
	justify-content: center;
	width: 50%;
	margin: 5rem auto;
	margin-bottom: 1rem;
}
.form-input {
	width: 300px;
	height: 2.5rem;
	margin: 1rem 2rem;
	font-size: 1.5rem;
	padding: 0 0.5rem;
	font-weight: bold;
}
.form-lable {
	font-size: 1.5rem;
	text-transform: capitalize;
	font-weight: bold;
}
.registration-form {
	width: 50%;
	margin: 3rem auto;
	display: flex;
	flex-direction: column;
	justify-content: center;
}
.registration-form div {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin: 0.5rem 2.5rem;
}
.login-span {
	margin-top: 0.5rem;
	text-align: center;
	font-weight: bold;
}
.registration-form span li {
	font-size: 1.3rem;
	font-weight: bold;
	margin: 0 1rem;
	cursor: pointer;
	color: #4db2d6;
	text-decoration: underline;
	font-style: italic;
}
.register-btn {
	cursor: pointer;
	padding: 1rem 2.3rem;
	margin: 1rem auto;
	font-weight: bold;
	font-size: 1rem;
	background: #4db2d6;
	border: none;
	border-radius: 5%;
	box-shadow: 1px 1px 5px #4db2d6;
}
.register-btn:hover {
	transition: all 1s;
	transform: scale(1.05);
}
.form > button {
	cursor: pointer;
	padding: 0 2.5rem;
	margin: 1rem;
	font-weight: bold;
	font-size: 1rem;
	background: #4db2d6;
	border: none;
	border-radius: 5%;
	box-shadow: 1px 1px 5px #4db2d6;
}
.ShoppingItems div li {
	font-size: 2rem;
	font-weight: bold;
	text-transform: capitalize;
}
i {
	color: #f01a38;
	cursor: pointer;
	size: 3rem;
}

i:hover {
	transform: scale(1.2);
}

.userDetails {
	font-weight: bold;
	font-size: 2rem;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
	width: 30%;
	margin: 4rem auto;
}

.profilediv {
	width: 70%;
	margin: 5rem auto;
	color: #333;
	font-size: 1.2rem;
	border: 1px solid black;
	box-shadow: 5px 5px 5px 1px #333;
}
span {
	font-weight: bold;
}
.profilemain {
	display: flex;
	align-items: center;
	flex-wrap: nowrap;
	justify-content: center;
}
.profileimg {
	margin: 1rem;
	background: url(/static/media/dummy.6d81db68.jpg) no-repeat;
	background-size: cover;
	background-position: center;
	width: 30%;
	height: 200px;
	border: 1px dotted black;
}
.profiledetails {
	width: 65%;
}
.profileitem,
.profileotheritem {
	margin: 1rem 5rem;
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.profileotheritem {
	margin: 1rem 10rem;
}
